export  function typeChange(grade){
    var changed = ''
    switch (grade) {
      case 1:
        changed = "初一";
        break;
      case 2:
        changed = "初二";
        break;
      case 3:
        changed = "初三";
        break;
      case 4:
        changed = "高一";
        break;
      case 5:
        changed = "高一";
        break;
      case 6:
        changed = "高一";
        break;
    }
    return changed
}
 



