export default {
  name: "success",
  props: {},
  // props:['xx'],
  data() {
    return {
      show1: false,
      show2: false,
      email: "",
      phone: "",
      userId: 3
    };
  },
  methods: {},
  created() {
    this.$axios
      .get("/api/user/" + this.userId + "/bound", {
        headers: {
          "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8"
        }
      })
      .then(res => {
        console.log(res);
        console.log(res.data.data.email)
        console.log(res.data.data.phone)
        // if ((res.data.data.email = null)) {
        //   this.email = "未绑定";
        // }
        // if ((res.data.data.phone = null)) {
        //   this.phone = "未绑定";
        // }
        if(res.data.data.phone == null){
            this.phone = '未绑定'
        }else {
            this.phone = res.data.data.phone
        }

        if(res.data.data.email !== null){
            this.email = res.data.data.email
        }else{
            this.email = '未绑定'
        }
      });
  }
};



