
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'

import first from "./first";
import second from "./second";
export default {
name: "collection",
components: {
Tab,
TabItem,
Sticky,
first,
second
},
props: {},
data() {
return {
isbc1: true,
isbc2: false,
selectCol: true,
first: "first",
second: "second",
currentView: "first"
};
},
methods: {
change1() {
this.isbc1 = true;
this.isbc2 = false;
},
change2() {
this.isbc2 = true;
this.isbc1 = false;
},
onItemClick(tabItem) {


this.currentView = tabItem;
},
gobackcard: function() {


this.$router.push({
path: "/card"
});
}
},

















};



