import Calendar from "vue-calendar-component";

export default {
  name: "calendar",
  data() {
    return {
      arr: ["2018/9/20", "2018/9/21"],
      title: "dd"
    };
  },
  methods: {
    update(titlex) {
      this.title = titlex;
    },
    openMask(index) {
      this.sendVal = true;
    },
    clickCancel() {
      console.log("点击了取消");
    },
    clickDanger() {
      console.log("这里是danger回调");
    },
    clickConfirm() {
      console.log("点击了confirm");
    },
    clickDay(data) {
      console.log(data); //选中某天
    },
    changeDate(data) {
      console.log(data); //左右点击切换月份
    },
    clickToday(data) {
      console.log(data); //跳到了本月
    }
  },
  components: {
    Calendar
  }
};



