
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import Qs from "qs";
export default {
name: "fit",
props: {},
components: {
Actionsheet,
Group,
XSwitch,
Toast,
XButton
},
data() {
return {
text2: "",
text: "绑定失败",
text1: "",
show2: false,
url: "http://img.wxcha.com/file/201801/24/30a267e348.jpg",
num: 500,
name: "",
position: "bottom",
showPositionValue: false,
showPositioncode: false,
show: false,
show1: false,
phone: "",
code: "",
sendcode: true,
little_time: 60,
userId: 2,
position1: "bottom"
};
},
methods: {
rule() {
this.show = !this.show;
},
cancelMask() {
this.show = false;
},
getCode() {
this.show1 = false;

this.showPositioncode = true;
var data = Qs.stringify({ phone: this.phone });
this.$axios
.post("/api/common/sendSms/" + this.userId, data, {
headers: {
"Content-Type": "application/x-www-form-urlencoded; charset=UTF-8"
}
})
.then(res => {
this.show1 = true;
console.log(res);
if (res.data.code == 200) {
this.sendcode = false;
this.little_time = 60;
this.text1 = "发送成功";
var Countdown = setInterval(() => {
this.little_time--;
if (this.little_time <= 0) {
this.sendcode = true;
clearInterval(Countdown);
}
}, 1000);
} else {
this.text1 = "手机号格式错误";
}
});
},
showPosition(position, show) {
this.show = false;

this.position = position;
this.showPositionValue = true;
var data = Qs.stringify({ code: this.code });
this.$axios
.post("/api/common/checkSms/" + this.userId, data, {
headers: {
"Content-Type": "application/x-www-form-urlencoded; charset=UTF-8"
}
})
.then(res => {
console.log(res);
if (res.data.code == 200) {
this.show2 = true;
this.show = false;
} else {
this.show = true;
this.text = res.data.msg;
}
});
}
}
};



