export default {
  name: "card",
  props: {},
  data() {
    return {
      url: "",
      grade: "",
      num: null,
      name: "",
      userId: 3,
      showbind: true
    };
  },
  methods: {},
  created() {
    this.$axios
      .get("/api/user/" + this.userId + "/card", {
        headers: {
          "Content-Type": "application/json; charset=UTF-8"
        }
      })
      .then(result => {
        if (result.data.code == 200) {
          console.log(result.data);
          this.name = result.data.data.nickname;
          this.num = result.data.data.score;
          this.url = result.data.data.headPic;
          this.grade = result.data.data.grade;
          console.log(result.data.data.boundStatus);
          if (result.data.data.boundStatus == 2) {
            this.showbind = false;
          }
        }else{
			alert('result.data.msg')
		}
      });
  }
};



