

import Vue from 'vue'
import VueRouter from 'vue-router'
import VueResource from 'vue-resource'
import 'element-ui/lib/theme-chalk/index.css'
import HelloWorld from './components/HelloWorld'
import Home from './components/Home'
import axios from 'axios'
import calendar from './components/calendar'
import swiper from './components/Swiper'
import upload from './components/Upload'
import can from './components/can'
import photo from './components/photo'
import dorm from './components/dorm'
import sign from './components/sign'
import card from './components/card'
import edit from './components/edit'
import collection from './components/collection'
import fit from './components/fit'
import success from './components/success'
import first from './components/first'
import second from './components/second'
import ElementUI from 'element-ui'
import fitphone from './components/fitphone'
import fitemail from './components/fitemail'
import Qs from 'qs'

import './components/fuction.css'

import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'

import article from './components/article'

import video from './components/video'


import Vuex from 'vuex' 


import vuexI18n from 'vuex-i18n'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Toast from 'vux/src/components/toast/index.vue'

import Actionsheet from 'vux/src/components/actionsheet/index.vue'

import XDialog from 'vux/src/components/x-dialog/index.vue'

import App from './App'


Vue.use(ElementUI);
Vue.use(MintUI)
Vue.component('swiper', Swiper)
Vue.component('toast', Toast)
Vue.component('x-dialog', XDialog)
Vue.component('actionsheet', Actionsheet)
Vue.use(Vuex); 

Vue.config.productionTip = false
Vue.use(VueRouter)
Vue.use(VueResource)
Vue.prototype.$axios = axios

axios.defaults.headers.post['Content-type'] = "application/json"



const router = new VueRouter({
routes: [{
path: '/',
component: resolve => require(['./components/Home'],resolve)
},
{
path: '/calendar',
component: resolve => require(['./components/calendar'],resolve)
},
{
path: '/swiper',
component: resolve => require(['./components/Swiper'],resolve)
},
{
path: '/upload',
component: resolve => require(['./components/Upload'],resolve)
},
{
path: '/can',
component: resolve => require(['./components/can'],resolve)
},
{
path: '/photo',
component: resolve => require(['./components/photo'],resolve)
},
{
path: '/dorm',
component: resolve => require(['./components/dorm'],resolve)
},
{
path: '/sign',
component: resolve => require(['./components/sign'],resolve)
},
{
path: '/card',
component: resolve => require(['./components/card'],resolve)
},
{
path: '/edit',
component: resolve => require(['./components/edit'],resolve)
},
{
path: '/collection',
component: resolve => require(['./components/collection'],resolve),
children:[
{path:'/collection/first',component:resolve => require(['./components/first'],resolve)},
{path:'/collection/second',component:resolve => require(['./components/second'],resolve)}
]
},
{
path: '/fit',
component: resolve => require(['./components/fit'],resolve),
children:[
{path:'/fit/fitphone',component:resolve => require(['./components/fitphone'],resolve)},
{path:'/fit/fitemail',component:resolve => require(['./components/fitemail'],resolve)}
]
},
{
path: '/success',
component: resolve => require(['./components/success'],resolve)
},

{
path: '/article',
component: resolve => require(['./components/article'],resolve)
},

{
path: '/video',
component: resolve => require(['./components/video'],resolve)
}
],
mode: 'history'

})



const store = new Vuex.Store({
modules: {
i18n: vuexI18n.store
}
});
Vue.use(vuexI18n.plugin, store);


Vue.mixin({ //全局混入
methods: {
goback: function () {
this.$router.push({
path: "/"
});
},
}
})
/* eslint-disable no-new */


import * as filter from './components/fiter';
Object.keys(filter).forEach(key => {
Vue.filter(key, filter[key])
});

new Vue({
router,
store,
el: '#app',
components: {
App
},
template: '<App/>'
})









