
import Calendar from "vue-calendar-component";
import Toast from 'vux/src/components/toast/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


export default {
name: "sign",
data() {
return {
arr: [],
title: "dd",
name: "",
num: 1,
url: "",
style: true,
show6: false,
position: "default",
showPositionValue: false,
showHideOnBlur: false,
show: false,
signmsg: "签到有礼",
dailyAchieve: false
};
},
methods: {
showPosition(position, num, arr) {
let date = new Date();


let today =
date.getFullYear() + "-" + (date.getMonth() + 1) + "-" + date.getDate();
console.log(today);
console.log(typeof today);
fetch("/api/user/sign", {
method: "post",
headers: {
"Content-type": "application/json"
},
body: JSON.stringify({
openId: 21,
signLast: today
})
})
.then(function(result) {
return result.json();
})
.then(data => {
console.log(data);
this.num = data.data.signNum;
this.position = position;
this.showPositionValue = true;
this.style = false;
this.arr.push(today);
localStorage.setItem("signstatus", "true");
});
},
rule() {
this.show = !this.show;
},
cancelMask() {
this.show = false;
},
detail() {}
},

created() {
this.$axios
.get("/api/user/1/card", {
headers: {
"Content-Type": "application/json; charset=UTF-8"
}
})
.then(result => {
console.log(result.data);
this.name = result.data.data.nickname;
this.num = result.data.data.score;
this.url = result.data.data.headPic;
});

this.$axios
.post("/api/user/sign/detail/21", {
headers: {
"Content-Type": "application/json; charset=UTF-8"
}
})
.then(result => {
console.log(result.data);
this.arr = result.data.datum;
let lastday = result.data.datum[result.data.datum.length - 1];
console.log(result.data.datum[result.data.datum.length - 1]);
let date = new Date();


let today =
date.getFullYear() +
"-" +
(date.getMonth() + 1) +
"-" +
date.getDate() +
" 00:00:00";
console.log(today);

if (localStorage.getItem("signstatus") && today == lastday) {
this.style = false;
} else {
this.style = true;
}
});
},
beforeUpdate() {},
directives: {
TransferDom
},
components: {
Calendar,
Toast,
XDialog,
Group,
XSwitch,
XButton
}
};



