
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


export default {
components: {
Actionsheet,
Group,
XSwitch,
Toast,
XButton
},
directives: {
TransferDom
},
data() {
return {
show2: false,
menus2: {
menu1: this.$t("拍照"),
menu2: this.$t("从相册选择")
},
url: "",
num: 500,
name: "",
position: "bottom",
showPositionValue: false,
text: "保存成功",
userId: 3
};
},
methods: {
click(key) {
console.log(key);
},
click5(key, item) {
console.log(key, item);
},
onDelete() {
this.showSuccess = true;
},
save() {
console.log(this.name);
},
showPosition(position, num) {
this.position = position;
this.showPositionValue = true;
}
},
created() {
this.$axios
.get("/api/user/" + this.userId + "/card", {
headers: {
"Content-Type": "application/json; charset=UTF-8"
}
})
.then(result => {
if (result.data.code == 200) {
console.log(result.data);
this.name = result.data.data.nickname;
this.num = result.data.data.score;
this.url = result.data.data.headPic;
this.grade = result.data.data.grade;
} else {
alert("result.data.msg");
}
});
}
};



